import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import {
  FCustomerScreenOrderFragment as TOrder,
  OrderStateCategoryEnum,
} from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { TCustomerNumber } from '../customerscreen.types'
import { useCustomerScreenOrders } from '../hooks/useCustomerScreenOrders'
import { TCustomerOrderState } from '../molecules/CustomerNumber'
import { CustomerNumbers } from '../molecules/CustomerNumbers'
import { MarqueeNumberStack } from '../molecules/MarqueeNumberStack'
import { StyledCustomerShelfHeadline } from '../molecules/StyledCustomerShelfHeadline'

type TProps = NoChildren & {
  categories: RoA<OrderStateCategoryEnum>
  sortByChange: 'ascend' | 'descend'
  maxRecent?: number
  headline: string
  headlineColor: string
}

const StyledCustomerScreenShelf = styled.section`
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 1fr;

  padding: 1rem;
  height: 100%;
`

const ARRAY_START = 0

export const CustomerScreenShelf = observer<TProps>(
  ({
    categories,
    sortByChange,
    maxRecent = Infinity,
    headline,
    headlineColor,
  }) => {
    const orders = useCustomerScreenOrders()

    const byChange = sortByChange === 'ascend' ? RM.ascend : RM.descend
    const sortOrders = RM.sortWith<TOrder>([
      RM.descend(RM.prop('orderStateCategory')),
      byChange(latestChange),
    ])

    if (orders === null) {
      return null
    }

    const customerNumbers = sortOrders(orders)
      .filter(order => categories.includes(order.orderStateCategory))
      .filter(order => order.orderNumber !== null)
      .map(orderToCustomerNumber)

    const recentNumbers = customerNumbers.slice(ARRAY_START, maxRecent)
    const olderNumbers = customerNumbers.slice(maxRecent)

    const showMarquee = maxRecent !== Infinity

    return (
      <StyledCustomerScreenShelf>
        <StyledCustomerShelfHeadline textColor={headlineColor}>
          {headline}
        </StyledCustomerShelfHeadline>
        <CustomerNumbers customerNumbers={recentNumbers} />
        {showMarquee && <MarqueeNumberStack customerNumbers={olderNumbers} />}
      </StyledCustomerScreenShelf>
    )
  },
)

type TOrderToCustomerState = Partial<
  Record<OrderStateCategoryEnum, TCustomerOrderState>
>
const orderToCustomerStateMapping: TOrderToCustomerState = {
  [OrderStateCategoryEnum.NewOrders]: 'new',
  [OrderStateCategoryEnum.Preparing]: 'preparing',
  [OrderStateCategoryEnum.ReadyToDeliver]: 'ready',
}

function orderToCustomerNumber(order: TOrder): TCustomerNumber {
  return {
    id: order.id,
    customerNumber: order.orderNumber!,
    orderState: orderToCustomerStateMapping[order.orderStateCategory]!,
  }
}

function latestChange(order: TOrder) {
  return order.orderTiming.map(RM.prop('createdAt')).reduce(RM.max)
}
