import { Trans } from '@lingui/macro'
import React from 'react'
import styled, { keyframes } from 'styled-components/macro'

import { Colors } from '../../../styling'
import { TCustomerNumber } from '../customerscreen.types'

type TProps = NoChildren & {
  customerNumbers: RoA<TCustomerNumber>
}

const marquee = keyframes`
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
`

const StyledMarqueeNumberStack = styled.div`
  --font-size: calc(1rem + 7vmin);

  display: grid;
  grid-template-rows: var(--font-size);

  > * {
    grid-row: 1;
    grid-column: 1 / -1;
  }

  > .number-line {
    white-space: nowrap;
    overflow: hidden;

    > .inner {
      display: inline-block;

      font-size: var(--font-size);
      color: ${Colors.green};

      padding-left: 100%;

      animation-name: ${marquee};
      animation-duration: 20s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
      animation-fill-mode: both;
    }
  }
`

export const MarqueeNumberStack: React.FC<TProps> = ({ customerNumbers }) => {
  const hasSomeNumbers = customerNumbers.length > 0

  return (
    <StyledMarqueeNumberStack>
      {hasSomeNumbers && (
        <div className="number-line">
          <span className="inner">
            <Trans>Previous:</Trans>
            {'  '}
            {customerNumbers.map(number => (
              <React.Fragment key={number.id}>
                {number.customerNumber}
                {'  '}
              </React.Fragment>
            ))}
          </span>
        </div>
      )}
    </StyledMarqueeNumberStack>
  )
}
