import React from 'react'
import styled from 'styled-components/macro'

import { TCustomerNumber } from '../customerscreen.types'
import { CustomerNumber } from './CustomerNumber'

type TProps = NoChildren & {
  customerNumbers: RoA<TCustomerNumber>
}

const StyledCustomerNumbers = styled.div`
  --min-col-width: calc(1rem + 15vmin + 10vmin);

  columns: 2 var(--min-col-width);
  column-fill: auto;
  overflow: hidden;
`

export const CustomerNumbers: React.FC<TProps> = ({ customerNumbers }) => (
  <StyledCustomerNumbers>
    {customerNumbers.map(number => (
      <CustomerNumber
        id={number.id}
        key={number.id}
        customerNumber={number.customerNumber}
        orderState={number.orderState}
      />
    ))}
  </StyledCustomerNumbers>
)
