import { createStatePersistence } from '@speedlo/hooks'
import React from 'react'

import { SelectControl } from '../../atoms/SelectControl'
import { FCompanyBranchFragment } from '../../graph/generated'
import { formatBranchName } from '../../helpers/formats'
import { useDropdownFilter } from '../../hooks/useDropdownFilter'
import { useDropdownMulti } from '../../hooks/useDropdownMulti'
import { useUserModel } from '../../hooks/useUserModel'
import { i18n } from '../../i18n'
import { idProp, RM } from '../../tools/ramda'

export const useBranchesFilterEditable = createStatePersistence<RoA<ID>>(
  'branches',
  [],
)

export const BranchesFilter = React.memo<NoChildren>(() => {
  const userModel = useUserModel()
  const [branches, setBranches] = useBranchesFilterEditable()

  const selectProps = {
    options: userModel.branches,
    placeholder: i18n.t`Filter branches...`,
    formatOptionLabel: formatBranchName,
    getOptionValue: idProp,
    hideOnSingleOption: true,
  }

  const dropdownProps = useDropdownMulti<FCompanyBranchFragment>({
    ...selectProps,
    onSelectionChange: setBranches,
    selected: branches,
  })

  const filterProps = useDropdownFilter<FCompanyBranchFragment>({
    getOptionFilter: formatBranchName,
  })

  return (
    <SelectControl<FCompanyBranchFragment>
      {...dropdownProps}
      {...filterProps}
      {...selectProps}
    />
  )
})

/**
 * Access to currently persisted list of branches for filtering purposes.
 *
 * By default it will use all user branches if none are currently set.
 * This behavior can be disabled with argument.
 *
 * @param withUserBranchesWhenEmpty
 */
export function useBranchesFilter(withUserBranchesWhenEmpty = true): ID[] {
  const userModel = useUserModel()
  const [persistedBranches, setPersistedBranches] = useBranchesFilterEditable()
  const persistenceKey = persistedBranches.join('|')

  let selectedBranches = React.useMemo(() => {
    return RM.intersection(userModel.branchesId, persistedBranches)
    // persistedBranches reference changes every time -> use persistenceKey to decide whether the persistence truly changed
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [persistenceKey, userModel.branchesId])

  const hasPersistedBranches = persistedBranches.length > 0
  const hasAccessToAllPersistedBranches =
    persistedBranches.length === selectedBranches.length

  React.useEffect(() => {
    if (hasPersistedBranches && !hasAccessToAllPersistedBranches) {
      setPersistedBranches(selectedBranches)
    }
  }, [
    hasAccessToAllPersistedBranches,
    hasPersistedBranches,
    selectedBranches,
    setPersistedBranches,
  ])

  const noBranchSelected = selectedBranches.length === 0
  const userHasOnlyOneBranch = userModel.branchesId.length === 1

  if (userHasOnlyOneBranch || (noBranchSelected && withUserBranchesWhenEmpty)) {
    selectedBranches = userModel.branchesId
  }

  const key = selectedBranches.join('|')
  // eslint-disable-next-line react-hooks/exhaustive-deps
  return React.useMemo(() => selectedBranches, [key])
}
