import { Trans } from '@lingui/macro'
import { Box } from '@material-ui/core'
import React from 'react'

import { Card } from '../../../atoms/Card'
import { PageLayoutGridItem } from '../../../atoms/PageLayoutGridItem'
import {
  BranchesFilter,
  useBranchesFilter,
} from '../../../components/filters/BranchesFilter'
import { SectorsFilter } from '../../../components/filters/SectorsFilter'
import { FormSubmitButton } from '../../../components/forms/FormSubmitButton'
import { i18n } from '../../../i18n'
import { PageLayoutResponsive } from '../../../molecules/PageLayoutResponsive'
import { Colors } from '../../../styling'
import { CustomerScreenSetupForm } from '../organisms/CustomerScreenSetupForm'

type TProps = NoChildren

export const CustomerScreenSetupPage: React.FC<TProps> = () => {
  const branches = useBranchesFilter(false)
  const noBranchSelected = branches.length === 0

  return (
    <CustomerScreenSetupForm>
      <PageLayoutResponsive pageTitle={i18n.t`Customer screen`}>
        <PageLayoutGridItem gridColumn="1 / span 9" gridRow="1">
          <Card>
            <BranchesFilter />
            <Box color={Colors.black}>
              <SectorsFilter />
            </Box>
            <FormSubmitButton color={Colors.green} disabled={noBranchSelected}>
              <Trans>Show customer screen</Trans>
            </FormSubmitButton>
          </Card>
        </PageLayoutGridItem>
      </PageLayoutResponsive>
    </CustomerScreenSetupForm>
  )
}

export default CustomerScreenSetupPage
