import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { useCanQuery } from '../../../hooks/useAccessGuard'
import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import {
  ROUTE_CUSTOMER_SCREEN,
  ROUTE_CUSTOMER_SCREEN_SETUP,
} from '../customerscreen.routes'
import CustomerScreenPage from '../pages/CustomerScreenPage'
import CustomerScreenSetupPage from '../pages/CustomerScreenSetupPage'

type TProps = NoChildren

export const CustomerScreenRouting: React.FC<TProps> = () => {
  const hasPermissions = useCanQuery('orders')

  if (!hasPermissions) {
    return <Route component={NotFoundPage} />
  }

  return (
    <Switch>
      <SecureRoute
        exact
        path={ROUTE_CUSTOMER_SCREEN}
        component={CustomerScreenPage}
      />
      <SecureRoute
        exact
        path={ROUTE_CUSTOMER_SCREEN_SETUP}
        component={CustomerScreenSetupPage}
      />
      <Route component={NotFoundPage} />
    </Switch>
  )
}

export default CustomerScreenRouting
