import React from 'react'
import styled, { keyframes } from 'styled-components/macro'

import { Colors } from '../../../styling'
import { TCustomerNumber } from '../customerscreen.types'

// enum values here has to correspond with class names bellow
export type TCustomerOrderState = 'new' | 'preparing' | 'ready'

type TProps = NoChildren & TCustomerNumber

const readyInit = keyframes`
  0% {
    font-size: 0;
    padding: calc(var(--base-font-size) / 2 + var(--padding));
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(var(--rotate-angle));
  }
  20% {
    transform: rotate(calc(-1 * var(--rotate-angle)));
  }
  30% {
    transform: rotate(var(--rotate-angle));
  }
  40% {
    transform: rotate(calc(-1 * var(--rotate-angle)));
  }

  50% {
    transform: rotate(var(--rotate-angle));
    font-size: calc(var(--base-font-size) + 2 * var(--padding));
    padding: 0px;
  }

  60% {
    transform: rotate(calc(-1 * var(--rotate-angle)));
  }
  70% {
    transform: rotate(var(--rotate-angle));
  }
  80% {
    transform: rotate(calc(-1 * var(--rotate-angle)));
  }
  90% {
    transform: rotate(var(--rotate-angle));
  }

  100% {
    transform: rotate(0deg);
  }
`

const StyledCustomerNumber = styled.div`
  transition: all 0.3s;
  width: fit-content;
  text-align: right;
  margin-right: 15%;
  margin-left: auto;

  /* class names here has to correspond with enum values above */
  &.new {
    --base-font-size: calc(1rem + 7vmin);
    --animation-duration: 0.4s;

    color: ${Colors.greyMiddle};
    font-size: var(--base-font-size);
    padding: 0;
  }

  &.preparing {
    color: ${Colors.white};
    font-size: calc(1rem + 10vmin);
  }

  &.ready {
    --padding: 1vmin;
    --base-font-size: calc(1rem + 15vmin);
    --animation-duration: 1s;
    --rotate-angle: 7deg;

    color: ${Colors.green};
    font-size: var(--base-font-size);
    padding: var(--padding);

    animation: ${readyInit} var(--animation-duration) linear;
  }
`

export const CustomerNumber = React.memo<TProps>(
  ({ customerNumber, orderState }) => {
    return (
      <StyledCustomerNumber className={orderState}>
        {customerNumber}
      </StyledCustomerNumber>
    )
  },
)
