import { useRouterContext } from '@speedlo/hooks'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useBranchesFilter } from '../../../components/filters/BranchesFilter'
import { QCustomerScreenOrderListOrders } from '../../../graph/generated'
import { ROUTE_CUSTOMER_SCREEN_SETUP } from '../customerscreen.routes'
import { TCustomerScreenFilter } from '../customerscreen.types'
import { useCustomerScreenDeck } from '../hooks/useCustomerScreenDeck'

type TOrder = QCustomerScreenOrderListOrders
type TProps = SomeChildren

export const loaderContext = React.createContext<RoA<TOrder> | null>(null)

export const CustomerScreenLoader = observer<TProps>(({ children }) => {
  const { history, location } = useRouterContext()
  const branches = useBranchesFilter(false)

  const filters: TCustomerScreenFilter | undefined = location.state?.filters
  const filtersPassed = filters !== undefined
  const branchesSelected = branches.length > 0

  const sectors = filters?.sectors ?? []
  const deck = useCustomerScreenDeck({ branches, sectors })

  React.useEffect(() => {
    if (!filtersPassed || !branchesSelected) {
      history.replace(ROUTE_CUSTOMER_SCREEN_SETUP)
    }
  }, [branchesSelected, filtersPassed, history])

  if (!filtersPassed || !branchesSelected) {
    return null
  }

  return (
    <loaderContext.Provider value={deck}>{children}</loaderContext.Provider>
  )
})
