import React from 'react'
import styled from 'styled-components/macro'

type TProps = SomeChildren & {
  toPrint: ReactNodes
}

const StyledPrinter = styled.div`
  & .toPrint {
    display: none;
  }
  @media print {
    & .toPrint {
      display: inline-block;
    }
    & .noPrint {
      display: none;
    }
  }
`

export const Printer: React.FC<TProps> = props => {
  return (
    <StyledPrinter>
      <div className="toPrint">{props.toPrint}</div>
      <div className="noPrint">{props.children}</div>
    </StyledPrinter>
  )
}
