import './SectorsFilter.css'

import { useXField } from '@speedlo/xform'
import React from 'react'
import DropdownTreeSelect, {
  DropdownTreeSelectProps,
  TreeNode,
} from 'react-dropdown-tree-select'

import {
  FCompanyBranchFragment,
  QFilterSectorsSectors,
  useQFilterSectors,
} from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { useUserModel } from '../../hooks/useUserModel'
import { i18n } from '../../i18n'
import { idProp, idPropEq, RM } from '../../tools/ramda'
import { useBranchesFilter } from './BranchesFilter'

const DropdownTreeSelectMemo = React.memo<DropdownTreeSelectProps & NoChildren>(
  ({ texts, data, onChange }) => {
    // this wrapper is necessary because of the silly implementation of the component
    // which basically resets the state on each render
    return <DropdownTreeSelect texts={texts} data={data} onChange={onChange} />
  },
)

type TProps = NoChildren & {
  markAsAvailable?(isAvailable: boolean): void
}

type TSector = QFilterSectorsSectors
type TBranch = FCompanyBranchFragment

export const SectorsFilter = React.memo<TProps>(({ markAsAvailable }) => {
  const userModel = useUserModel()
  const branches = useBranchesFilter()
  const hasSingleBranch = branches.length === 1

  const { getStash } = useQFilterSectors({
    branches,
  })
  const sectors = getStash(data => data.sectors)
  const hasAnySectors = sectors !== null && sectors.length > 0

  const treeData = React.useMemo(() => {
    if (sectors === null) {
      return null
    }
    if (hasSingleBranch) {
      return buildForSingleBranch(sectors)
    }
    return buildWithBranches(
      sectors,
      userModel.branches.filter(branch => branches.includes(branch.id)),
    )
  }, [branches, hasSingleBranch, sectors, userModel])

  const field = useXField<RoA<ID>>('sectors')

  React.useEffect(() => {
    if (markAsAvailable) {
      markAsAvailable(hasAnySectors)
    }
  }, [hasAnySectors, markAsAvailable])

  const onChange = React.useCallback(
    (currentNode: TreeNode, selectedNodes: TreeNode[]) => {
      const selectedSectorsId = selectedNodes.reduce(
        (acc, node) => {
          if (isBranchNode(node)) {
            // with branch selected, its root sectors needs to be used
            // in the filter instead
            const branchNode = treeData!.find(idPropEq(node.value))
            if (branchNode && branchNode.children) {
              acc.push(...(branchNode.children as RoA<TSector>).map(idProp))
            }
          } else {
            acc.push(node.value)
          }
          return acc
        },
        [] as ID[],
      )
      field.setValue(selectedSectorsId)
    },
    [field, treeData],
  )

  const texts = React.useMemo(
    () => ({
      placeholder: i18n.t`Filter by sectors`,
      noMatches: i18n.t`No sectors`,
    }),
    [],
  )

  if (!hasAnySectors) {
    return null
  }

  return (
    <DropdownTreeSelectMemo
      texts={texts}
      data={treeData!}
      onChange={onChange}
    />
  )
})

function buildForSingleBranch(sectors: RoA<TSector>) {
  return getTreeFromFlatData({
    flatData: sectors,
    mapper: node => ({
      label: node.name,
      value: node.id,
    }),
    getParentKey(node) {
      return node.parent ? node.parent.id : null
    },
    getKey(node) {
      return node.id
    },
  })
}

const branchKey = (branchId: ID) => `branch-${branchId}`
function isBranchNode(node: TreeNode) {
  return node.value.startsWith('branch')
}

function buildWithBranches(sectors: RoA<TSector>, branches: RoA<TBranch>) {
  const sectorsPerBranch = RM.groupBy(
    sector => sector.companyBranch.id,
    sectors,
  )

  const flatData = [
    ...sectors,
    ...branches.filter(branch => {
      return (
        sectorsPerBranch[branch.id] && sectorsPerBranch[branch.id].length > 0
      )
    }),
  ]

  return getTreeFromFlatData({
    flatData,
    mapper: (node: TSector | TBranch) => ({
      label: node.name,
      id: isSector(node) ? node.id : branchKey(node.id),
      value: isSector(node) ? node.id : branchKey(node.id),
    }),
    getParentKey(node) {
      if (isSector(node)) {
        if (node.parent) {
          return node.parent.id
        }
        // sectors without parent gets the branch as parent
        return branchKey(node.companyBranch.id)
      }
      return null // is branch, no parent
    },
    getKey(node) {
      if (isSector(node)) {
        return node.id
      }
      return branchKey(node.id)
    },
  })
}

function getTreeFromFlatData<T extends Record<string, any>, R>({
  flatData,
  mapper,
  getKey,
  getParentKey,
}: {
  flatData: RoA<T>
  mapper(node: T): R
  getKey(node: T): string
  getParentKey(node: T): string | null
}): Array<T & R & { children: T[] }> {
  if (!flatData) {
    return []
  }

  const rootKey = '@@'

  const childrenToParents = { [rootKey]: [] as T[] }
  flatData.forEach(child => {
    const parentKey = getParentKey(child) || rootKey

    if (parentKey in childrenToParents) {
      childrenToParents[parentKey].push(child)
    } else {
      childrenToParents[parentKey] = [child]
    }
  })

  if (!(rootKey in childrenToParents)) {
    return []
  }

  const trav = (parent: T) => {
    const parentKey = getKey(parent)
    if (parentKey in childrenToParents) {
      return {
        ...parent,
        ...mapper(parent),
        children: childrenToParents[parentKey].map(child => trav(child)),
      }
    } else {
      return {
        ...parent,
        ...mapper(parent),
        children: ARRAY_EMPTY,
      }
    }
  }

  return childrenToParents[rootKey].map(child => trav(child))
}

function isSector(node: TSector | TBranch): node is TSector {
  return Reflect.has(node, 'parent')
}
