import {
  OrderStateCategoryEnum,
  QCustomerScreenOrderListQuery,
  SCustomerScreenOrderListSubscription,
  useQCustomerScreenOrderList,
  useSCustomerScreenOrderList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'

export const PREPARING_STATES = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
]
export const READY_STATES = [OrderStateCategoryEnum.ReadyToDeliver]
const REQUIRED_STATES = [...PREPARING_STATES, ...READY_STATES]

export const useCustomerScreenDeck = ({ branches, sectors }) => {
  const skip = branches.length === 0
  const variables = {
    branches,
    sectors,
  }

  const { getStash, updateQuery, refetch } = useQCustomerScreenOrderList(
    {
      ...variables,
      categories: REQUIRED_STATES,
    },
    { skip },
  )

  useOnConnectionRestored(refetch)

  useSCustomerScreenOrderList(
    {
      ...variables,
      // subscription needs to watch for finished state to remove such orders from view
      categories: REQUIRED_STATES.concat(OrderStateCategoryEnum.Finished),
    },
    {
      skip,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => handleSubscriptionUpdate(dataUpdate, prev))
        }
      },
    },
  )

  return getStash(data => data.orders)
}

function handleSubscriptionUpdate(
  update: SCustomerScreenOrderListSubscription,
  prev: QCustomerScreenOrderListQuery,
) {
  const { order } = update.updatedOrder

  if (!order || !prev.orders) {
    return prev
  }

  if (!REQUIRED_STATES.includes(order.orderStateCategory)) {
    return { orders: prev.orders.filter(o => o.id !== order.id) }
  }

  let orderExists = false
  const orders = prev.orders.map(prevOrder => {
    if (prevOrder.id === order.id) {
      orderExists = true
      return order
    }

    return prevOrder
  })

  if (!orderExists) {
    orders.unshift(order)
  }

  return { orders }
}
