import { yup } from '@speedlo/xform'
import { IFormState } from 'react-form'

import { i18n } from '../i18n'
import { parseNumber } from './numeral'

export function isFormValid(formState: IFormState) {
  return !formState.errors || Object.keys(formState.errors).length === 0
}

export function fieldIsRequiredMsg(fieldDescription: string) {
  return i18n.t`Field ${fieldDescription} cannot be empty`
}

export function fieldIsNumberMsg(fieldDescription: string) {
  return i18n.t`Field ${fieldDescription} must be a number`
}

export function fieldIsPositiveNumberMsg(fieldDescription: string) {
  return i18n.t`Field ${fieldDescription} must be a positive number`
}

export const validateAsNumberField = (fieldDescription: string) =>
  yup
    .number()
    .default(0)
    .transform((_, origValue) => parseNumber(origValue))
    .test(
      'number-field',
      fieldIsNumberMsg(fieldDescription),
      value => !isNaN(value),
    )

export const ARRAY_EMPTY: RoA<any> = []
