import { useRouterContext } from '@speedlo/hooks'
import { XForm } from '@speedlo/xform'
import { toJS } from 'mobx'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useBranchesFilter } from '../../../components/filters/BranchesFilter'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { ROUTE_CUSTOMER_SCREEN } from '../customerscreen.routes'
import { TCustomerScreenFilter } from '../customerscreen.types'

type TProps = SomeChildren

export const CustomerScreenSetupForm = observer<TProps>(({ children }) => {
  const { history } = useRouterContext()
  const branches = useBranchesFilter(false)

  const onFilterSubmit = React.useCallback(
    (values: TCustomerScreenFilter) => {
      const noBranchSelected = branches.length === 0

      if (noBranchSelected) {
        // the submit button is disabled by the same condition, this shouldnt happen to casual user
        return
      }

      const filters = toJS(values)
      history.push(ROUTE_CUSTOMER_SCREEN, { filters })
    },
    [branches.length, history],
  )

  return (
    <XForm initialValues={{ sectors: ARRAY_EMPTY }} onSubmit={onFilterSubmit}>
      {children}
    </XForm>
  )
})
